.checkout-fields {
	&__group {
		margin-bottom: 26px;
		
		&:last-child {
			margin-bottom: 0;
		}
	}
}

.checkout-fieldgroup {
	&__header {
		display: grid;
		grid-template-columns: auto auto;
		justify-content: space-between;
		align-items: baseline;
		margin-bottom: 10px;
	}

	&__title {
		font-family: "Roboto Slab", sans-serif;
		font-size: 18px;
		line-height: 32px;
	}

	&__description {
		font-size: 14px;
		line-height: 32px;
		color: #181818;
		font-weight: 700;
		
		a {
			color: #febd70;
			text-decoration: none;
		}
	}

	&__fields-row {
		display: grid;
		grid-template-columns: 1fr 1fr;
		gap: 20px;
		margin-bottom: 10px;
		
		@media (min-width: 1024px) {
			margin-bottom: 20px;
		}

		&:last-child {
			margin-bottom: 0;
		}
		
		&--single {
			grid-template-columns: 1fr;
		}

		&--checkbox {
			margin-top: -5px;
			margin-bottom: 26px;
			margin-left: 12px;
		
			@media (min-width: 1024px) {
				margin-bottom: 26px;
			}
		}
	}

	&--shipping {
		.checkout-fieldgroup__header {
			margin-bottom: 24px;
		}
	}
}


// Checkout page
.input-text {
	width: 100%;
	max-width: 100%;
	height: 54px;
	padding-right: 14px;
	padding-left: 14px;
	border: none;
	border-radius: 4px;
	background-color: #f4ede4;
	outline-color: #f4ede4;
	font-size: 14px;
	font-family: "PT Sans", sans-serif;
	transition: all 0.15s ease-in-out;
	
	@media (min-width: 1024px) {
		padding-right: 20px;
		padding-left: 20px;
	}
	
	&:focus {
		outline-color: #febd70;
	}
}

.checkbox {
	position: relative;
	display: flex;
	align-items: center;
	column-gap: 18px;
	flex-grow: 1;
	font-size: 12px;
	color: #181818;
	cursor: pointer;
	
	&::before {
		content: "";
		flex-shrink: 0;
		width: 14px;
		height: 14px;
		border-radius: 3px;
		background-color: #F4EDE4;
		background-position: center center;
		background-repeat: no-repeat;
		transition: all .2s ease-in-out;
	}
	
	input {
		position: absolute;
		clip: rect(0, 0, 0, 0);
	}

	&--checked::before {
		background-color: #13141a;
		background-image: url("data:image/svg+xml,%3Csvg width='14' height='11' viewBox='0 0 14 11' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4.277 10.302a1 1 0 0 0 1.418.003l7.594-7.594A1 1 0 0 0 13.3 1.31l-.015-.016a1 1 0 0 0-1.426-.012L4.984 8.156 2.127 5.3A.997.997 0 0 0 .714 6.705l3.563 3.597Z' fill='%23FEBD70'/%3E%3C/svg%3E");
		background-size: 10px auto;
	}

	.optional {
		display: none;
	}
}

.radio {
	position: relative;
	display: flex;

	input {
		position: absolute;
		clip: rect(0, 0, 0, 0);
		
		&:checked ~ label::before {
			background-color: #febd70;
		}
	}	
	
	label {
		display: flex;
		align-items: center;
		column-gap: 18px;
		flex-grow: 1;
		font-size: 12px;
		color: #181818;
		cursor: pointer;
		
		&::before {
			content: "";
			flex-shrink: 0;
			width: 14px;
			height: 14px;
			border-radius: 50%;
			background-color: #dbd5cd;
			background-position: center center;
			background-repeat: no-repeat;
			transition: all .2s ease-in-out;
		}
	}
}

.radio-group {
	&__title {
		margin-bottom: 10px;
		font-weight: bold;
		font-size: 18px;
		font-family: "Roboto Slab", sans-serif;
