.stages {
	&__list {
		list-style: none;
		display: flex;
		justify-content: center;
		align-items: center;
		gap: 5px;
		
		@media (min-width: 1024px) {
			padding-bottom: 12px;
			border-bottom: 1px solid #f4ede4;
		}
	}

	&__item {
		display: flex;
		align-items: center;
		gap: 5px;
		font-size: 12px;
		line-height: 28px;
		color: #6A4436;
		// cursor: pointer;
		
		&::after {
			content: "";
			width: 14px;
			height: 21px;
			background: url("data:image/svg+xml,%3Csvg version='1.2' baseProfile='tiny-ps' xmlns='http://www.w3.org/2000/svg' width='14' height='21'%3E%3Cstyle%3E%3C/style%3E%3Cg id='Checkout - information'%3E%3Cg id='Group 290'%3E%3Cg id='Group 242'%3E%3Cpath id='Path 509' d='m3.5 3.5 7 7-7 7' style='opacity:.502;fill:none;stroke:%236a4436;stroke-width:2'/%3E%3C/g%3E%3C/g%3E%3C/g%3E%3C/svg%3E") 0 0 no-repeat;
		}

		&--current {
			font-weight: 700;
			color: #13141A;
		}

		&:last-child::after {
			display: none;
		}
	}
}

.stage-blocks {
	padding: 20px 15px;
	
	@media (min-width: 1024px) {
		// padding: 3px 33px 32px;
		// padding: 3px 30px 32px 32px;
		padding: 3px 30px 12px 32px;
	}
}

.stage-block {
