	}
}

.multistage-form {
	@media (max-width: 1023px) {
		padding: 11px 0;
	}
	
	@media (min-width: 1024px) {
		display: grid;
		grid-template-columns: 1fr 300px;
		grid-template-rows: auto 1fr;
		// align-items: start;
		align-items: stretch;
		border-radius: 4px;
		filter: drop-shadow(0px 3px 3.5px rgba(0,0,0,0.16));
		background-color: #fffbf6;
	}

	@media (min-width: 1200px) {
		grid-template-columns: 1fr 363px;
	}
	
	&__summary {
		@media (max-width: 1023px) {
			border-radius: 4px;
			filter: drop-shadow(0 3px 3.5px rgba(0, 0, 0, 0.16));
			background-color: #fffbf6;
		}

		@media (min-width: 1024px) {
			grid-area: 1 / 2 / 3 / 3;
			// background-color: #f4ede4;
			background-color: #faf4ed;
		}
	}
	
	&__stages-list {
		margin-bottom: 12px;
		
		@media (min-width: 1024px) {
			margin-top: 14px;
			margin-bottom: 22px;
		}
	}

	&__stage-block {
		&:not(.multistage-form__stage-block--current) {
			display: none;
		}
	}

	&__buttons {
		@media (min-width: 1024px) {
			display: grid;
			grid-template-columns: auto auto;
			justify-content: space-between;
			align-items: center;
			// padding: 0 32px 30px;
			padding: 0 30px 30px 32px;
			
			&--justify-end {
				justify-content: end;
			}
		}
	}
}

.woocommerce {
	&__multistage-form {
		@media (max-width: 767px) {
			margin-right: -15px;
			margin-left: -15px;
		}

		@media (min-width: 1024px) {
			// margin: 32px 0;
			margin: 32px 0 120px;
		}
	}

	.woocommerce-checkout-review-order-table {
		padding: 14px 16px;
		
		@media (min-width: 1024px) {
			padding: 8px 22px 30px;
		}
		
		&__header {
			position: relative;
			display: grid;
			grid-template-columns: auto auto;
			justify-content: space-between;
			align-items: center;
			margin-bottom: 20px;
			padding-right: 52px;
			
			@media (max-width: 767px) {
				padding-right: 59px;
				cursor: pointer;
			}
			
			@media (max-width: 1023px) {
				&::after {
					content: "";
					position: absolute;
					top: 0;
					right: 13px;
					bottom: 0;
					width: 20px;
					height: 13px;
					margin: auto;
					background: url("data:image/svg+xml,%3Csvg version='1.2' baseProfile='tiny-ps' xmlns='http://www.w3.org/2000/svg' width='20' height='13'%3E%3Cstyle%3E%3C/style%3E%3Cg id='Checkout Summary'%3E%3Cpath id='Path 524' d='m3 10 7-7 7 7' style='fill:none;stroke:%2313141a;stroke-width:2'/%3E%3C/g%3E%3C/svg%3E") 0 0 no-repeat;
					transition: all .2s ease-in-out;
				}
			}
		}

		&__title {
			font-size: 16px;
			line-height: 32px;
			color: #181818;
			font-weight: 700;
			font-family: "PT Sans";
			opacity: 0.5;
			
			@media (min-width: 1024px) {
				opacity: 1;
			}
		}

		&__price {
			font-size: 14px;
			line-height: 20px;
			color: #992b13;
			font-weight: 700;
			font-family: "Roboto Slab";
		}

		&__cart-items {
			margin-bottom: 25px;
		}

		.cart_item {
			margin-bottom: 20px;
			
			&:last-child {
				margin-bottom: 0;
			}
		}

		.cart-subtotal,
		.order-total {
			display: grid;
			grid-template-columns: auto auto;
			justify-content: space-between;
			padding-top: 20px;
			border-top: 1px solid #dbd5cd;
			font-weight: 500;
			font-family: "Roboto Slab", sans-serif;
			font-size: 14px;
			line-height: 20px;
		}

		.cart-subtotal {
			margin-bottom: 10px;
		}
		
		.product-thumb {
			margin-bottom: 10px;
			border-radius: 4px;
			background-color: #ffffff;
			border: 1px solid #f4ede4;
			
			img {
				display: block;
				border-radius: 4px;
			}
		}

		.product-name-and-total {
			display: grid;
			grid-template-columns: auto auto;
			justify-content: space-between;
			align-items: start;
			gap: 20px;
			padding-bottom: 10px;
			border-bottom: 1px solid #dbd5cd;
		}
		
		.product-name {
			font-weight: 700;
			font-family: "Roboto Slab", sans-serif;
			font-size: 14px;
			line-height: 20px;
			
			a {
				color: #13141A;
				text-decoration: none;
			}
			
			.variation {
				display: none;
			}
		}

		.product-total {
			font-size: 14px;
			line-height: 20px;
			color: #992b13;
			font-weight: 700;
			font-family: "Roboto Slab";
		}

		&__close-btn {
			width: 100%;
			margin-top: 20px;
		}

		&--collapsed {
			.woocommerce-checkout-review-order-table__header {
				margin-bottom: 0;
				
				&::after {
					transform: rotate(180deg);
				}
			}

			.woocommerce-checkout-review-order-table__cart-content {
				display: none;
			}
		}
	}
}

